<template>
<v-container>
<v-layout row grap>
<v-flex xs6 offset-xs3>
    <h1>Login</h1>
    <v-text-field label="email" placeholder="email"
    :value="loginEmail"
    @input="setLoginEmail">
    </v-text-field>
    <v-text-field label="password" placeholder="password"
    type="password" autocomplete="new-password"
   :value="loginPassword"
    @input="setLoginPassword">
    </v-text-field>
    <v-alert type="error" :value="loginError">
      {{loginError}}
    </v-alert>
    <v-btn color="black" dark @click="login">
    <v-icon class="mr-2">fingerprint</v-icon>
    Login
    </v-btn>
</v-flex>
</v-layout>
</v-container>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex';

export default {
  computed: {
    ...mapState('authentication', [
      'loginEmail',
      'loginPassword',
      'loginError',
    ]),
  },
  methods: {
    ...mapMutations('authentication', [
      'setLoginEmail',
      'setLoginPassword',
    ]),
    ...mapActions('authentication', [
      'login',
    ]),
  },
};
</script>

